<template>
  <div>
    <a-row :gutter="24">
      <a-col :sm="24" :md="12" :xl="12" :style="{ marginBottom: '24px' }">
        <chart-card
          :loading="loading"
          title=""
          :total="8846 | NumberFormat"
        >
          <a-tooltip title="" slot="action">
            <a-icon type="info-circle-o" />
          </a-tooltip>
          <div>
            <mini-area />
          </div>
          <template slot="footer"
            ><span> {{ "1234" | NumberFormat }}</span></template
          >
        </chart-card>
      </a-col>
      <a-col :sm="24" :md="12" :xl="12" :style="{ marginBottom: '24px' }">
        <chart-card
          :loading="loading"
          title=""
          :total="560 | NumberFormat"
        >
          <a-tooltip title="" slot="action">
            <a-icon type="info-circle-o" />
          </a-tooltip>
          <div>
            <mini-bar />
          </div>
          <template slot="footer"> <span>50%</span></template>
        </chart-card>
      </a-col>
    </a-row>

    <div
      class="antd-pro-pages-dashboard-analysis-twoColLayout"
      :class="!isMobile && 'desktop'"
    >
      <a-row :gutter="24" type="flex" :style="{ marginTop: '24px' }">
        <a-col :xl="12" :lg="24" :md="24" :sm="24" :xs="24">
          <a-card
            :loading="loading"
            :bordered="false"
            title=""
            :style="{ height: '100%' }"
          >
            <a-dropdown
              :trigger="['click']"
              placement="bottomLeft"
              slot="extra"
            >
              <a class="ant-dropdown-link" href="#">
                <a-icon type="ellipsis" />
              </a>
              <a-menu slot="overlay">
                <a-menu-item>
                  <a href="javascript:;"></a>
                </a-menu-item>
                <a-menu-item>
                  <a href="javascript:;"></a>
                </a-menu-item>
              </a-menu>
            </a-dropdown>
            <a-row :gutter="68">
              <a-col :xs="24" :sm="12" :style="{ marginBottom: ' 24px' }">
                <number-info :total="12321" :sub-total="17.1">
                  <span slot="subtitle">
                    <span></span>
                    <a-tooltip title="" slot="action">
                      <a-icon
                        type="info-circle-o"
                        :style="{ marginLeft: '8px' }"
                      />
                    </a-tooltip>
                  </span>
                </number-info>
                <!-- miniChart -->
                <div>
                  <mini-smooth-area
                    :style="{ height: '45px' }"
                    :dataSource="searchUserData"
                    :scale="searchUserScale"
                  />
                </div>
              </a-col>
              <a-col :xs="24" :sm="12" :style="{ marginBottom: ' 24px' }">
                <number-info :total="2.7" :sub-total="26.2" status="down">
                  <span slot="subtitle">
                    <span></span>
                    <a-tooltip title="" slot="action">
                      <a-icon
                        type="info-circle-o"
                        :style="{ marginLeft: '8px' }"
                      />
                    </a-tooltip>
                  </span>
                </number-info>
                <!-- miniChart -->
                <div>
                  <mini-smooth-area
                    :style="{ height: '45px' }"
                    :dataSource="searchUserData"
                    :scale="searchUserScale"
                  />
                </div>
              </a-col>
            </a-row>
            <div class="ant-table-wrapper">
              <a-table
                row-key="index"
                size="small"
                :columns="searchTableColumns"
                :dataSource="searchData"
                :pagination="{ pageSize: 5 }"
              >
                <span slot="range" slot-scope="text, record">
                  <trend :flag="record.status === 0 ? 'up' : 'down'">
                    {{ text }}%
                  </trend>
                </span>
              </a-table>
            </div>
          </a-card>
        </a-col>
        <a-col :xl="12" :lg="24" :md="24" :sm="24" :xs="24">
          <a-card
            class="antd-pro-pages-dashboard-analysis-salesCard"
            :loading="loading"
            :bordered="false"
            title=""
            :style="{ height: '100%' }"
          >
            <h4></h4>
            <div>
              <!-- style="width: calc(100% - 240px);" -->
              <div>
                <v-chart
                  :force-fit="true"
                  :height="405"
                  :data="pieData"
                  :scale="pieScale"
                >
                  <v-tooltip :showTitle="false" dataKey="item*percent" />
                  <v-axis />
                  <!-- position="right" :offsetX="-140" -->
                  <v-legend dataKey="item" />
                  <v-pie position="percent" color="item" :vStyle="pieStyle" />
                  <v-coord type="theta" :radius="0.75" :innerRadius="0.6" />
                </v-chart>
              </div>
            </div>
          </a-card>
        </a-col>
      </a-row>
    </div>
  </div>
</template>

<script>
import moment from "moment";
import {
  ChartCard,
  MiniArea,
  MiniBar,
  Trend,
  NumberInfo,
  MiniSmoothArea,
} from "@/components";
import { baseMixin } from "@/store/app-mixin";

const barData = [];
const barData2 = [];
for (let i = 0; i < 12; i += 1) {
  barData.push({
    x: `${i + 1}`,
    y: Math.floor(Math.random() * 1000) + 200,
  });
  barData2.push({
    x: `${i + 1}`,
    y: Math.floor(Math.random() * 1000) + 200,
  });
}

const searchUserData = [];
for (let i = 0; i < 7; i++) {
  searchUserData.push({
    x: moment().add(i, "days").format("YYYY-MM-DD"),
    y: Math.ceil(Math.random() * 10),
  });
}
const searchUserScale = [
  {
    dataKey: "x",
    alias: "",
  },
  {
    dataKey: "y",
    alias: "",
    min: 0,
    max: 10,
  },
];

const searchTableColumns = [
  {
    dataIndex: "index",
    title: "",
    width: 90,
  },
  {
    dataIndex: "keyword",
    title: "",
  },
  {
    dataIndex: "count",
    title: "",
  },
  {
    dataIndex: "range",
    title: "",
    align: "right",
    sorter: (a, b) => a.range - b.range,
    scopedSlots: { customRender: "range" },
  },
];
const searchData = [];
for (let i = 0; i < 50; i += 1) {
  searchData.push({
    index: i + 1,
    keyword: `-${i}`,
    count: Math.floor(Math.random() * 1000),
    range: Math.floor(Math.random() * 100),
    status: Math.floor((Math.random() * 10) % 2),
  });
}

const DataSet = require("@antv/data-set");

const sourceData = [
  { item: "", count: 27.2 },
  { item: "", count: 21 },
  { item: "", count: 17 },
  { item: "", count: 13 },
  { item: "", count: 10 },
  { item: "", count: 6 },
  { item: "", count: 5.8 },
];

const pieScale = [
  {
    dataKey: "percent",
    min: 0,
    formatter: ".0%",
  },
];

const dv = new DataSet.View().source(sourceData);
dv.transform({
  type: "percent",
  field: "count",
  dimension: "item",
  as: "percent",
});
const pieData = dv.rows;

export default {
  name: "Analysis",
  mixins: [baseMixin],
  components: {
    ChartCard,
    MiniArea,
    MiniBar,

    Trend,
    NumberInfo,
    MiniSmoothArea,
  },
  data() {
    return {
      loading: true,

      // 
      searchUserData,
      searchUserScale,
      searchTableColumns,
      searchData,

      barData,
      barData2,

      //
      pieScale,
      pieData,
      sourceData,
      pieStyle: {
        stroke: "#fff",
        lineWidth: 1,
      },
    };
  },
  created() {
    setTimeout(() => {
      this.loading = !this.loading;
    }, 1000);
  },
};
</script>

<style lang="less" scoped>
.extra-wrapper {
  line-height: 55px;
  padding-right: 24px;

  .extra-item {
    display: inline-block;
    margin-right: 24px;

    a {
      margin-left: 24px;
    }
  }
}

.antd-pro-pages-dashboard-analysis-twoColLayout {
  position: relative;
  display: flex;
  display: block;
  flex-flow: row wrap;
}

.antd-pro-pages-dashboard-analysis-salesCard {
  height: calc(100% - 24px);
  /deep/ .ant-card-head {
    position: relative;
  }
}

.dashboard-analysis-iconGroup {
  i {
    margin-left: 16px;
    color: rgba(0, 0, 0, 0.45);
    cursor: pointer;
    transition: color 0.32s;
    color: black;
  }
}
.analysis-salesTypeRadio {
  position: absolute;
  right: 54px;
  bottom: 12px;
}
</style>
